.navigation {
    width: 100%;
    height: 100vh;
    padding: .5rem;
    display: block;
    background: #640ad2;
    color: white;
}

.logo {
    width: 100%;
    height: 5rem;
    padding: .5rem;
    text-align: center;
    overflow: hidden;
    margin-bottom: 2rem;
}

.logo img {
    width: 30%;
}

.logo small {
    display: block;
    font-size: 10px;
}

.profile {
    width: 100%;
    height: 10rem;
    padding: .5rem;
    text-align: center;
    overflow: hidden;

}

.photo {
    width: 100px;
    height: 100px;
    border-radius: 50%;
    background: white;
    margin: .5rem;
    margin: 0 auto;
    overflow: hidden;
}

.photo img {
    width: 100px;
    height: 100px;
    border-radius: 50%;
}

.url {
    width: 100%;
    height: auto;
    display: block;
    padding: 1rem;
}

.url ul {
    width: 100%;
    padding: 0;
    margin: 0;
    list-style: none;
}

.url ul a {
    text-decoration: none;
    color: white;
    font-size: 13px;
}

.url ul a li {
    width: 100%;
    padding: 0;
    margin: 0;
    line-height: 3rem;
    float: left;
}

.url ul a li svg {
    margin-right: .5rem;
}

.navbar {
    width: 100%;
    height: 3rem;
    padding: .5rem;
    border-bottom: 1px solid whitesmoke;
    overflow: hidden;
}

.title {
    width: 80%;
    height: auto;
    display: block;
    float: left;
}

.name {
    width: 20%;
    height: auto;
    display: block;
    float: left;
}