.welcome {
    width: 100%;
    height: 15rem;
    background: #640ad2;
    border-radius: 30px;
}

.avatar {
    width: 30%;
    height: 15rem;
    float: left;
}

.avatar img {
    width: 60%;
    margin-top: -2rem;
}

.name {
    width: 70%;
    height: 15rem;
    float: left;
    padding: 5rem;
    color: white;
}

.card {
    width: 100%;
    height: 10rem;
    border-radius: 10px;
    background: white;
    padding: 1rem;
    box-shadow: rgba(200, 200, 206, 0.05) 0px 4px 16px, rgba(200, 200, 206, 0.05) 0px 8px 32px;
}

.icon {
    width: 100%;
    height: 5rem;
    text-align: center;
}

.icon {
    width: 50px;
    height: 50px;
    border-radius: 50%;
    margin: 0 auto;
    padding: 7px;
}

.text {
    width: 100%;
    height: 5rem;
    text-align: center;
    padding: 1rem;
}