.main {
    width: 100%;
    height: 100vh;
    background: linear-gradient(0deg, rgba(100, 10, 210, 0.8), rgba(100, 10, 210, 0.8)), url(../assets/tech.jpg);
    padding: 10rem
}

.login {
    width: 40%;
    height: auto;
    border-radius: 20px;
    margin: 0 auto;
    color: white;
    padding: 0;
}

.loginGrid {
    display: grid;
    grid-template-columns: 100%;
}

.loginCard {
    width: 100%;
    height: auto;
    padding: 1rem;
    display: block;
}

.img {
    padding: 0;
}

.img img {
    width: 100%;
    min-height: 25rem;
}


.formPadding {
    padding: .5rem;
}

.logo {
    width: 100%;
    height: 5rem;
    text-align: center;
    overflow: hidden;
}

.logo img {
    width: 10%;
}

.forms {
    padding: .5rem 3rem;
}

.forms input[type="email"],
[type="password"],
[type="text"],
[type="date"],
[type="number"] {
    border-radius: 20px;
    padding: 1rem;
    background: white;
}

.forms select {
    border-radius: 20px;
    padding: 1rem;
    background: white;
}

.forms input[type="submit"] {
    border-radius: 0;
    padding: 1rem;
    border-radius: 20px;
}

.forms input::placeholder {
    font-size: 13px;

}

.app {
    width: 100%;
    height: auto;
    min-height: 100vh;
    display: block;
    padding: 0;
}

.appGrid {
    display: grid;
    grid-template-columns: 10% 90%;
}

.contents {
    width: 100%;
    height: auto;
    min-height: 100vh;
    display: block;
    padding: 1rem;
    /* background: rgba(245, 245, 245, 0.8); */
}

.contentGrid {
    display: grid;
    grid-template-columns: 70% 30%;
}

.cardGrid {
    padding: 1rem 0rem;
    display: grid;
    grid-template-columns: 24% 24% 24% 24%;
    grid-gap: 1%;
}

.purple {
    background: #F2EBFB;
    color: #713BDB;
}

.cyan {
    background: #EEF9FF;
    color: #49B8FF;
}

.gold {
    background: #FEF3EF;
    color: #FE8A57;
}

.red {
    background: #FFEFF2;
    color: #FD5181;
}

.profiles {
    width: 100%;
    height: auto;
    display: block;
    padding: 1rem 2rem;
}

.profileGrid {
    display: grid;
    grid-template-columns: 18% 60% 18%;
    grid-gap: 2%;
}

.profile {
    width: 100%;
    height: auto;
    min-height: 10rem;
    display: block;
}

.photo {
    width: 200px;
    height: 200px;
    border-radius: 50%;
    background: #000;
    overflow: hidden;
}

.photo img {
    width: 200px;
    height: 200px;
    border-radius: 50%;
}

.trophy {
    width: 200px;
    height: 100px;
    padding: .3rem;
    float: left;
}

.icons {
    width: 20%;
    height: 100px;
    float: left;
}

.icons svg {
    font-size: 1.5rem;
}

.text {
    width: 80%;
    height: 100px;
    float: left;
}

.text p {
    font-size: 11px;
    margin-bottom: 0;
}

.text small {
    font-size: 10px;
}

.formPadding a {
    text-decoration: none;
    color: white;
}




/* phone-only */
@media (max-width: 599px) {
    .main {
        padding: 1rem
    }

    .login {
        width: 100%;
    }

    .appGrid {
        display: grid;
        grid-template-columns: 100%;
    }

    .profileGrid {
        display: grid;
        grid-template-columns: 100%;
        grid-gap: 2%;
    }

    .forms {
        padding: .5rem;
    }
}

/* .tablet-portrait-up  */
@media (min-width: 600px) {
    .main {
        padding: 1rem
    }

    .login {
        width: 100%;
    }
}

/* tablet-portait-only */
@media (min-width: 600px) and (max-width: 899px) {

    .main {
        padding: 1rem
    }

    .login {
        width: 100%;
    }
}

/* tablet-landscape-up */
@media (min-width: 900px) {

    .main {
        padding: 7rem
    }

    .login {
        width: 50%;
    }
}

/* tablet-landscape-only */
@media (min-width: 900px) and (max-width: 1199px) {

    .main {
        padding: 10rem
    }

    .login {
        width: 40%;
    }
}

/* desktop-only */
@media (min-width: 1300px) and (max-width: 1799px) {

    .main {
        padding: 10rem
    }

    .login {
        width: 40%;
    }

}

/* big-desktop-up */
@media (min-width: 1800px) {

    .main {
        padding: 10rem
    }

    .login {
        width: 40%;
    }
}